<template>
<div class="container">
  <Header>
    <div title="订单支付" class="title-right" slot="right" @click="goOrderList(0)">订单中心</div>
  </Header>
  <div class="top-title">
    为了保证您的资产安全，请复制以下链接到电脑端连接U盾支付
  </div>
  <div class="content">
    <p class="content-text">
      {{payUrl}}<span class="symbol-text">***</span>
    </p>
    <div class="button" v-if="showCopy" @click="copy">复制链接</div>
    <div class="botton-check" v-else @click="goOrderList(0)">查询支付状态</div>
  </div>
  <div class="bottom-tip">
    说明：<br />
    <span style="visibility: hidden;">说明</span>1、支付链接生成后10分钟内有效，若支付链接过期或丢失，请到订单列表对该订单重新发起支付；<br />
    <span style="visibility: hidden;">说明</span>2、支付成功后，若订单支付状态未更新，可能因网络原因导致的支付结果延迟，请五分钟后重新刷新列表查询订单列表支付状态，若仍异常请联系客服处理。<br />
    <span style="visibility: hidden;">说明</span>3、目前支持银行：工商银行、中国银行。
  </div>
</div>
</template>

<script>
import Header from '@/components/header';
import ClipboardJS from 'clipboard'; // 复制插件

export default {
  name: 'b2b-pay',
  components: {
    Header,
  },
  data() {
    return {
      showCopy: true,
      payUrl: '',
      originUrl: '',
      showDialog: true,
    }
  },
  mounted() {
    const payUrlStr = this.$route.params.payUrl;
    this.originUrl = payUrlStr
    this.payUrl = payUrlStr.substring(0, 26);
  },
  methods: {
    goOrderList(tag) {
      // this.$dialog.alert({
      //   message: '订单所包含商户数量超过一家，无法使用公对公付款，请优化订单内商家数量或选用其他支付方式。',
      //   className: 'dialog-custom',
      // });
      this.$router.push({
        path: `/orderList?tagChecked=${tag}`,
      });
    },
    copy() {
      let copyContent = this.originUrl;
      const clipboard = new ClipboardJS('.button', {
        text: function (trigger) {
          return copyContent;
        }
      });
      clipboard.on('success', () => {
        this.$toast('复制链接成功！');
        let timer = setTimeout(() => {
          clearTimeout(timer)
          this.showCopy = false;
        }, 120000);
      });
      clipboard.on('error', () => {
        this.$toast('复制失败');
      });
    },
  },
}
</script>

